<template>
  <el-dialog :title="dialogTitle" @open="handleOpen">
    <div class="box-area-1">
      <div class="item-icon-box">
        <img class="item-icon-image" :src="item.icon" />
      </div>

      <div>
        <h2>{{ item.name }}</h2>
        <h4>{{ item.title }}</h4>
        <el-form label-width="100">
          <el-form-item label="状态">
            <MyInstallingState :modelValue="item.state" />
          </el-form-item>
          <el-form-item label="版本"> {{ packVersion }} </el-form-item>
          <el-form-item label="大小"> {{ packSize }} </el-form-item>
          <el-form-item label="更新时间"> {{ packDate }} </el-form-item>
        </el-form>
      </div>
    </div>

    <div class="box-area-2">
      <el-divider />
      <p>{{ item.description }}</p>
      <el-divider />
    </div>

    <div class="box-area-3">
      <el-button
        type="success"
        @click="install"
        :disabled="!canInstall"
        v-show="canInstall"
      >
        安装
      </el-button>

      <el-button
        type="default"
        link
        @click="upgrade"
        :disabled="!canUpgrade"
        v-show="canUpgrade"
      >
        升级
      </el-button>

      <el-button
        type="default"
        link
        @click="reinstall"
        :disabled="!canReInstall"
        v-show="canReInstall"
      >
        重新安装
      </el-button>

      <el-button
        type="danger"
        @click="uninstall"
        :disabled="!canUninstall"
        v-show="canUninstall"
      >
        卸载
      </el-button>
    </div>
  </el-dialog>
</template>

<script>
import MyInstallingState from "./installing-state.vue";
import TimeJS from "@/js/time.js";
import BytesJS from "@/js/bytes.js";

function PackageVersionInfoMaker() {
  this.latest = null;
  this.current = null;
}

PackageVersionInfoMaker.prototype = {
  add(pack) {
    if (pack.installed) {
      this.current = pack;
    }
    this.latest = pack;
  },

  stringifyItems(field, formatter) {
    let o1 = this.current;
    let o2 = this.latest;
    let list = [o1, o2];
    let str = "";
    let sep = "";
    let id = null;
    for (var i in list) {
      let item = list[i];
      if (item == null) {
        continue;
      }
      let value = item[field];
      if (value == null) {
        continue;
      }
      if (item.id == id) {
        break;
      } else {
        id = item.id;
      }
      let text = formatter(value);
      str = str + sep + text;
      sep = " ==> ";
    }
    return str;
  },

  getVersion() {
    return this.stringifyItems("version", (value) => {
      return value;
    });
  },
  getSize() {
    return this.stringifyItems("size", (value) => {
      return BytesJS.StringifySize(value);
    });
  },
  getDate() {
    return this.stringifyItems("release_at", (value) => {
      return TimeJS.StringifyTimestamp(value);
    });
  },
};

export default {
  name: "settings-plug-ins-item-detail-dialog",

  components: { MyInstallingState },

  computed: {
    dialogTitle() {
      return "插件详情";
    },

    canInstall() {
      return !this.item.installed;
    },
    canUninstall() {
      return this.item.installed;
    },
    canReInstall() {
      return this.item.installed;
    },
    canUpgrade() {
      return this.item.state == "NEWVERSION";
    },
  },

  data() {
    return {
      item: {},
      packVersion: "",
      packSize: "",
      packDate: "",
    };
  },

  methods: {
    install() {
      this.fireAction("install");
    },

    reinstall() {
      this.fireAction("re-install");
    },

    uninstall() {
      this.fireAction("uninstall");
    },

    upgrade() {
      this.fireAction("upgrade");
    },

    fireAction(name) {
      let target = this.target;
      let e = { name, target };
      this.$emit("action", e);
      this.close();
    },

    prepareItem(src) {
      let str = JSON.stringify(src);
      let dst = JSON.parse(str);
      let packs = dst.packages;
      let maker = new PackageVersionInfoMaker();
      if (packs != null) {
        for (var i in packs) {
          maker.add(packs[i]);
        }
      }
      this.packVersion = maker.getVersion();
      this.packSize = maker.getSize();
      this.packDate = maker.getDate();
      return dst;
    },

    handleOpen() {
      let value = this.target;
      if (value == null) {
        return;
      }
      this.item = this.prepareItem(value);
    },

    close() {
      this.$emit("update:modelValue", false);
    },
  },

  props: {
    target: Object,
  },

  watch: {},
};
</script>

<style scoped>
.item-icon-image {
  max-width: 96px;
  max-height: 96px;
  border-color: rgba(255, 254, 174, 0);
  border-style: solid;
  border-width: 5px;
  border-radius: 3px;
  padding: 10px;
}

.item-icon-box {
  margin-right: 30px;
}

.box-area-1 {
  display: flex;
  flex-direction: row;
}
</style>
