
<template>
  <div>
    <el-pagination
      :current-page="currentPage"
      :page-size="pageSize"
      :page-sizes="[5, 10, 20, 50, 100, 200, 500]"
      :background="background"
      layout="total, sizes, prev, pager, next"
      :total="theCountItemsTotal"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
    />
  </div>
</template>
  
<script>
const store_prefix = "soft_set/";

export default {
  name: "settings-plug-ins-table",

  computed: {
    currentPage() {
      return this.$store.getters[store_prefix + "current_page"];
    },

    pageSize() {
      return this.$store.getters[store_prefix + "page_size"];
    },

    theCountItemsTotal() {
      return this.$store.getters[store_prefix + "total"];
    },
  },

  data() {
    return {
      background: false,
    };
  },

  methods: {
    handleSizeChange(value) {
      let size = value;
      let index = 0;
      this.$store.commit(store_prefix + "update", { index, size });
    },

    handleCurrentChange(value) {
      let index = value - 1;
      this.$store.commit(store_prefix + "update", { index });
    },
  },

  mounted() {},
};
</script>
  
<style scoped>
</style>
  