
<template>
  <div>
    <el-button @click="refresh"> 刷新 </el-button>
    <el-button @click="handleClickUpdate"> 更新插件列表 </el-button>
  </div>
</template>
  
<script>
export default {
  name: "settings-plug-ins-table",

  computed: {},

  data() {
    return {};
  },

  methods: {
    fetch() {
      this.$store.dispatch("soft_set/fetch");
    },

    refresh() {
      this.fetch();
    },

    handleClickUpdate() {
      this.$store.dispatch("soft_pack/update_sources");
    },
  },

  mounted() {
    this.fetch();
  },
};
</script>
  
