<template>
  <div>
    <el-icon class="icon-info" v-show="canUpgrade"><InfoFilled /></el-icon>
    {{ text }}
    <el-icon class="icon-success" v-show="installed"><SuccessFilled /></el-icon>
  </div>
</template>

<script setup>
import { InfoFilled, SuccessFilled } from "@element-plus/icons-vue";
</script>

<script>
const installing_state_map = {
  na: "不可用",
  INSTALLED: "已安装",
  NEWVERSION: "可升级",
  AVAILABLE: "未安装",
};

export default {
  name: "settings-plug-ins-installing-state",

  computed: {
    text() {
      let key = this.modelValue;
      let text = installing_state_map[key];
      if (text == null) {
        text = key;
      }
      return text;
    },

    canUpgrade() {
      return this.modelValue == "NEWVERSION";
    },

    installed() {
      return this.modelValue == "INSTALLED";
    },
  },

  props: {
    modelValue: String,
  },
};
</script>

<style scoped>
.icon-success {
  color: green;
}

.icon-info {
  color: blue;
}
</style>
