
<template>
  <div class="settings-index-page">
    <h1>插件</h1>

    <MyToolbar @action="handleAction" />

    <MySearch class="search-box" />

    <MyPager class="pager-box" />

    <MyTable @action="handleAction" />

    <MyEdit
      method="POST"
      v-model:enabled="theAddEnabled"
      v-model:item="theAddItem"
    />
    <MyEdit
      method="PUT"
      v-model:enabled="theEditEnabled"
      v-model:item="theEditItem"
    />
  </div>
</template>
    
  <script>
import MyTable from "./table.vue";
import MyPager from "./pager.vue";
import MySearch from "./search.vue";
import MyToolbar from "./toolbar.vue";
import MyEdit from "./edit.vue";

import { ElLoading } from "element-plus";

export default {
  name: "settings-executables",
  components: {
    MyTable,
    MyPager,
    MySearch,
    MyToolbar,
    MyEdit,
  },

  data() {
    return {
      theAddItem: {},
      theAddEnabled: false,
      theEditItem: {},
      theEditEnabled: false,
    };
  },

  methods: {
    handleAction(e) {
      let name = e.name;
      if (name == "edit") {
        this.edit(e.item);
      } else if (name == "add") {
        this.add();
      } else if (name == "install") {
        this.installSet(e.target);
      } else if (name == "uninstall") {
        this.uninstallSet(e.target);
      } else if (name == "re-install") {
        this.reInstallSet(e.target);
      } else if (name == "upgrade") {
        this.upgradeSet(e.target);
      } else {
        // NOP
      }
    },

    add() {
      this.theAddItem = {
        icon: "/image/exe-icons/default.png",
      };
      this.theAddEnabled = true;
    },

    installSet(item) {
      let text = "正在安装...";
      let options = { fullscreen: true, text };
      const loadingInstance = ElLoading.service(options);
      this.$store.dispatch("soft_set/install", { item }).finally(() => {
        loadingInstance.close();
      });
    },

    uninstallSet(item) {
      let text = "正在卸载...";
      let options = { fullscreen: true, text };
      const loadingInstance = ElLoading.service(options);
      this.$store.dispatch("soft_set/uninstall", { item }).finally(() => {
        loadingInstance.close();
      });
    },

    reInstallSet(item) {
      let text = "正在重新安装...";
      let options = { fullscreen: true, text };
      const loadingInstance = ElLoading.service(options);
      this.$store.dispatch("soft_set/reinstall", { item }).finally(() => {
        loadingInstance.close();
      });
    },

    upgradeSet(item) {
      let options = { fullscreen: true, text: "正在升级..." };
      const loadingInstance = ElLoading.service(options);
      this.$store.dispatch("soft_set/upgrade", { item }).finally(() => {
        loadingInstance.close();
      });
    },

    edit(item) {
      this.theEditItem = item;
      this.theEditEnabled = true;
    },
  },
};
</script>
  
  <style scoped>
.settings-index-page {
  text-align: left;
}

.search-box {
  max-width: 300px;
}

.pager-box {
  margin-top: 50px;
  margin-bottom: 20px;
}
</style>
  