
<template>
  <div>
    <el-table :data="items">
      <el-table-column prop="name" label="名称">
        <template #default="scope">
          <el-button link type="primary" @click="openItemDetail(scope.row)">
            {{ scope.row.name }}
          </el-button>
        </template>
      </el-table-column>

      <el-table-column prop="title" label="标题">
        <template #default="scope">
          <span :title="scope.row.description"> {{ scope.row.title }} </span>
        </template>
      </el-table-column>

      <el-table-column prop="version" label="版本"></el-table-column>

      <el-table-column prop="state" label="状态">
        <template #default="scope">
          <MyInstallingState :modelValue="scope.row.state" />
        </template>
      </el-table-column>

      <el-table-column label="操作" width="170px">
        <template #default="scope">
          <el-button link type="primary" @click="openItemDetail(scope.row)"
            >详情</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <MyItemDetailDialog
      class="plug-in-detail-dialog"
      v-model="displayDetailDialog"
      :target="theDetailDialogTarget"
      @action="handleAction"
    />
  </div>
</template>
  
<script>
import MyItemDetailDialog from "./item-detail-dialog.vue";
import MyInstallingState from "./installing-state.vue";

function nop() {}

export default {
  name: "settings-plug-ins-table",

  components: { MyItemDetailDialog, MyInstallingState },

  computed: {
    items() {
      return this.$store.getters["soft_set/list"];
    },
  },

  data() {
    return {
      theDetailDialogTarget: {},
      displayDetailDialog: false,
    };
  },

  methods: {
    openItemDetail(item) {
      this.theDetailDialogTarget = item;
      this.displayDetailDialog = true;
    },

    handleAction(e) {
      this.$emit("action", e);
    },
  },

  mounted() {},
};
</script>
  
<style scoped>
.plug-in-detail-dialog {
  max-width: 512px;
}
</style>
